import Img2Base64 from "@/common/img2Base64.js"
export default {
	name: 'compInput',
	behaviors: ['uni://form-field'],
	data() {
		return {
			img: Img2Base64,
			modalName: null,
			value: ''
		}
	},

	methods: {
		onInput(e) {
			this.$emit('input', e.detail.value)
		},
		showModal(e) {
			console.log(123);
			this.modalName = "DialogModal"
		},
		hideModal(e) {
			this.modalName = null
		},
		to() {
			this.modalName = null
			this.value = ""
			uni.showToast({
				title: "提交成功",
				icon: "success"
			})
		},
	}
}
